@charset "UTF-8";
.sortable thead th {
  cursor: pointer;
}
.sortable thead th.no-sort {
  pointer-events: none;
}
.sortable thead th::after, .sortable thead th::before {
  transition: color 0.1s ease-in-out;
  font-size: 1.2em;
  color: transparent;
}
.sortable thead th::after {
  margin-left: 3px;
  content: "▸";
}
.sortable thead th:hover::after {
  color: inherit;
}
.sortable thead th.dir-d::after {
  color: inherit;
  content: "▾";
}
.sortable thead th.dir-u::after {
  color: inherit;
  content: "▴";
}
.sortable thead th.indicator-left::after {
  content: "";
}
.sortable thead th.indicator-left::before {
  margin-right: 3px;
  content: "▸";
}
.sortable thead th.indicator-left:hover::before {
  color: inherit;
}
.sortable thead th.indicator-left.dir-d::before {
  color: inherit;
  content: "▾";
}
.sortable thead th.indicator-left.dir-u::before {
  color: inherit;
  content: "▴";
}